<template>
  <div id="app" class='main_page'>
       <el-container>
           <el-header class="head">
                <el-row class="head_top" :gutter="10">
                  <el-col class="nav_logo" :xs="24" :sm="24" :md="4" :lg="4" :xl="4"><img class="logo" :src="logo"><div class="hidden-md-and-up"><el-button  @click="show3 = !show3" size="small" class="more" type="primary" icon="el-icon-more"></el-button></div></el-col>
                  <el-col :xs="24" :sm="24" :md="20" :lg="20" :xl="20">
                      <el-collapse-transition>
                       <ul v-show="show3"  class="transition-box">
                         <li><router-link to="/">Home</router-link></li>
                         <li><router-link to="Message">Message</router-link></li>
                         <li><router-link to="Music">Music</router-link></li>
                         <li><router-link to="About_me">About_me</router-link></li>
                         <li><el-input placeholder="请输入内容"  clearable></el-input></li>
                       </ul>  
                      </el-collapse-transition>
                  </el-col>
                </el-row>
           </el-header>

           <el-main>
                 <router-view></router-view>
           </el-main>

           <el-footer class="el-footer" style="">
               <div type="text" @click="centerDialogVisible = true"><img :src="send"></div>
           </el-footer>

       </el-container>
       <div>
        <el-dialog  class="data_form"
                title="留言"
                :visible.sync="centerDialogVisible"
                width="100%"
                left>
                    <el-form  :model="ruleForm">
                        <h1>姓名</h1>
                        <el-input prop="test"
                        placeholder="请输入内容"
                        v-model="ruleForm.name"
                        clearable>
                        </el-input>

                        <h1>留言</h1>
                        <el-input type="textarea" prop="pass"
                        placeholder="请输入内容"
                        v-model="ruleForm.liuy"
                        clearable>
                        </el-input>

                    <el-form-item>
                        <el-button :plain="true" type="primary" @click="submitForm('ruleForm');centerDialogVisible = false">提交</el-button>
                        <el-button @click="resetForm('ruleForm')">重置</el-button>
                    </el-form-item>
                    </el-form>
                    <div class="ly_result">
                         <h1>{{ruleForm.name}}:</h1>
                         <h2>{{ruleForm.liuy}}</h2>
                    </div>

        </el-dialog>
    </div>
       
  </div>
</template>

<script>
import logo from './assets/aislogo.svg'
import send from '@/assets/send.svg'

export default {
  name: 'App',
  data(){
    var sOh;
    if(document.body.clientWidth > 992){sOh = true}else{sOh = false};
    return {
      logo:logo,
      screenWidth: document.body.clientWidth,
      centerDialogVisible: false,
      show3: sOh,
      send:send,
      
      ruleForm: {
          name: '',
          liuy: ''
        }
    }
  },

  mounted() {
            const that = this;
            window.onresize = () => {
                var sOh;
                if(that.screenWidth > 992){sOh = true}else{sOh = false};
                return (() => {
                    that.show3 = sOh 
                    window.screenWidth = document.body.clientWidth
                    that.screenWidth = window.screenWidth
                })()
            };
  },

  methods: {
      submitForm(formName) {
          this.$message({
          showClose: true,
          message: '提交成功!',
          type: 'success'
          });
          this.resetForm()
      },

      resetForm() {
          this.ruleForm.name = '';
          this.ruleForm.liuy = ''
      }
  }
}
</script>

<style>
html, body, div, ul, li, h1, h2, h3, h4, h5, h6, p, dl, dt, dd, ol, form, input, textarea, th, td, select {
    margin: 0;
    padding: 0;
}
*{box-sizing: border-box;
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  font-size:18px;
  margin: 0;
  padding: 0;}
ul,ol {
    list-style: none;
}
a {
    text-decoration: none;
    color: #232323;
}
img {
    border: none;
    vertical-align: middle;
}

.data_form input{
     width:100%;
     margin:20px 0px;
    }

    .data_form textarea{
        width:100%;
        margin:20px 0px;
    }

.data_form h1{text-align:left;font-weight:normal}

.ly_result{padding:10px;border:2px dotted #d2d2d2}

@media screen and (min-width: 992px) {
    .el-footer div{
        position:fixed;
        border-radius:50%;
        width:40px;
        height:40px;
        bottom:10px;
        left:-20px;
        background:#409EFF;
        margin-left:50%;
        line-height:40px;
        text-align:Center;
        cursor:pointer;
        -moz-box-shadow:2px 2px 5px #333333; -webkit-box-shadow:2px 2px 5px #333333; box-shadow:2px 2px 5px #333333;
    }
    
    .el-footer img{
        width:50%;
    }

    

    .main_page .head {
        width:992px;
        margin:auto
    }
    .e_mail_send{width:50px;
                 height:50px;
                 background:#409EFF;
                 border-radius:50%;
                 line-height:50px;
                 text-align:center
                 }

    .e_mail_send img{
                 width:35%;
                 height:35%;
                 line-height:50px;
                 text-align:center
                 }             
    }

@media screen and (max-width: 992px) {

    .el-footer div{
        position:fixed;
        border-radius:50%;
        width:40px;
        height:40px;
        bottom:10px;
        left:-20px;
        background:#409EFF;
        margin-left:50%;
        line-height:40px;
        text-align:Center;
        cursor:pointer;
        -moz-box-shadow:2px 2px 5px #333333; -webkit-box-shadow:2px 2px 5px #333333; box-shadow:2px 2px 5px #333333;
    }
    
    .el-footer img{
        width:50%;
        }
        
    .main_page .head {
        width:100%;
    }
    .main_page .head ul li{
        text-align:Center;
        width:100%;
        border-bottom:1px solid #DCDFE6;
    }
    .main_page .head ul li:last-child{
        text-align:Center;
        width:100%;
        border-bottom:2px solid #409EFF;
    }
}

 ul{
  width:100%;
  line-height:60px;
  float:left
}

.main_page .head{margin-top:5px;z-index:2;}

.main_page .head .nav_logo{
  text-align:center;
}

.head_top{background:#fff}

.main_page .head .nav_logo .more{
  position:absolute;
  top:12px;
  right:10px
}

.main_page .head .nav_logo .more i{
  color:#fff;
}

.main_page .logo{
   height:60px
}

.main_page .head ul li{
  padding:0 30px;
  float:left
}

.main_page .head ul li:last-child{
  padding:0px;
  line-height:56px
}



</style>


