import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import message from '@/components/message'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/message',
      name: 'message',
      component: message
    },
    {
      path: '/Music',
      name: 'Music',
      component: message
    },
    {
      path: '/About_me',
      name: 'About_me',
      component: message
    }
  ]
})



