<template>
  <div class="home">
    <!-- <h1>{{ msg }}</h1> -->
    <div class="main_text">
      <h1><span>Personal Space</span></h1>
      <p class="hidden-sm-and-down" style="color:#999;font-size:20px;padding-bottom:20px">
        A man can <span style="color:#409eff;font-weight:600;">succeed</span> at almost anything<br>for whichhe has unlimited enthusiasm.
      </p>
      <h2 style="color:#777;" class="hidden-sm-and-down"><img :src="author"><span>Qi Jianzhong</span>  <img :src="wechat"  @mousemove="code_show" @mouseout="code_hide"><span  @mousemove="code_show" @mouseout="code_hide">Wechat:495067507</span></h2>
      <h3 class="hidden-sm-and-down"><a target="_new" v-bind:href="gitUrl"><el-button class="git_text" icon="el-icon-star-on" type="primary">Github.com for Author</el-button></a></h3>
      <div v-show="ecode" class="ecode"><img :src="ecode_jpg"></div>
    
      <el-radio-group  class="hidden-sm-and-up" v-model="tabPosition" style="margin-bottom: 20px;margin-top: 20px;">
                
                  <el-radio-button label="top">Qi</el-radio-button>
      
                  <el-popover ref="popover2" placement="bottom" title="Wechat ID" width="200" trigger="hover" content="495067507"></el-popover>
                  <el-radio-button   label="right" v-popover:popover2>Wechat</el-radio-button>

                  <el-popover ref="popover3" placement="bottom-end" title="Github for Author" width="200" trigger="hover" content=""><a :href="gitUrl">{{gitUrl}}</a></el-popover>
                  <el-radio-button   label="left" v-popover:popover3>Git</el-radio-button>

      </el-radio-group>
 
    </div>
    <div class="main_bg"><img :src="main_bg"></div>

    <div class="my_obj">

        <div class="obj_title"><span>My Object</span></div>
        <div class="obj_content">
           <el-row :gutter="100">
            <a target="_new" v-bind:href="item.it1.url"><el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8"><div class="grid-content bg-purple obj_a"><img :src="item.it1.img"></div><h1>Beacon tour</h1></el-col></a>
            <a target="_new" v-bind:href="item.it2.url"><el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8"><div class="grid-content bg-purple-light obj_b"><img :src="item.it2.img"></div><h1>大嘉商品追溯</h1></el-col></a>
            <a target="_new" v-bind:href="item.it3.url"><el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8"><div class="grid-content bg-purple obj_c"><img :src="item.it3.img"></div><h1>978酒吧</h1></el-col></a>
          </el-row>
        </div>

    </div>
    
  </div>
</template>

<script>

import main_bg from '@/assets/main_bg.jpg'
import author from '@/assets/author.svg'
import wechat from '@/assets/wechat.svg'
import ecode_jpg from '@/assets/ecode.jpg'
import git from '@/assets/git.jpg'

import beaconice from '@/assets/beaconice.png'
import wine from '@/assets/978.png'
import cjtrace from '@/assets/cjtrace.png'

export default {
  name: 'Home',
  data () {
    return {
      main_bg:main_bg,
      git:git,
      ecode_jpg:ecode_jpg,
      ecode:false,
      gitUrl:"https://github.com/qijianzhong",
      author:author,
      wechat:wechat,
      item:{
        it1:{img:beaconice,name:'Beacon tour',url:'http://demo.beaconice.com.cn/'},
        it2:{img:cjtrace,name:'大嘉商品追溯',url:'http://www.cgtrace.com/'},
        it3:{img:wine,name:'978酒坊',url:'http://www.978wine.com'}
      }
    }
  },
  methods: {
    code_show: function (events) {
        this.ecode = true;
    },
    code_hide: function (events) {
        this.ecode = false;
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


    .my_obj{
      margin-top:80px;
      width:100%;
      float:left;
      text-align:center;
      }

      .my_obj a{color:#666}

@media screen and (min-width: 992px) {
    .my_obj .obj_title {height:80px}
    .my_obj .obj_title span{border-bottom:1px solid #66b1ff;padding-bottom:5px;margin-bottom:5px}

    .my_obj .obj_content{
      width:100%
    }

    .my_obj .obj_content .obj_a,.my_obj .obj_content .obj_b,.my_obj .obj_content .obj_c{
      height:100px;line-height:100px;margin-bottom:20px
    }
    .my_obj .obj_content .obj_a img{height:70px}
    .my_obj .obj_content .obj_a img,.my_obj .obj_content .obj_b img,.my_obj .obj_content .obj_c img{
    }

    .my_obj .obj_content .obj_a{
      width:100%;
      background:#f3f3f3;
      border-top:2px solid blue
    }

    .my_obj .obj_content .obj_b{
      width:100%;
      background:#eba91f;
      border-top:2px solid Red
    }

    .my_obj .obj_content .obj_c{
      width:100%;
      background:#1a1a1a;
      border-top:2px solid green
    }


    .home {
        width:992px;
        margin:auto;
        margin-top:100px
    }
    .main_bg{
    text-align:right
    }
    .main_text{
      padding-top:110px;
      width:490px
    }
    .main_bg{
      width:500px
    }
    .main_text h1{height:60px;font-weight: 600;}

    .main_text h1 span{
          font-size:40px;
          border-bottom:1px solid #409EFF;
          padding-bottom:5px
    }
    .main_text h2{height:30px;}
    .main_text h2 img{height:20px;margin-right:10px}
}

@media screen and (max-width: 992px) {
    .home {
        width:100%;
    }
    .my_obj h1{margin-bottom:50px}
    .my_obj .obj_title span{border-bottom:1px solid #66b1ff;padding-bottom:5px;margin-bottom:5px}
    .my_obj .obj_title {margin-bottom:80px}

    .my_obj .obj_content .obj_a,.my_obj .obj_content .obj_b,.my_obj .obj_content .obj_c{
      height:150px;line-height:150px;margin-bottom:5px;
    }

    .my_obj .obj_content .obj_a{background: #f3f3f3;
                                border-top: 2px solid blue;}

    .my_obj .obj_content .obj_b{background:#eba91f;
                                border-top:2px solid Red}

    .my_obj .obj_content .obj_c{background: #1a1a1a;
                                border-top: 2px solid green;}



    .my_obj .obj_content .obj_a img{width:120px}

    .my_obj .obj_content .obj_b img{width:120px}

    .my_obj .obj_content .obj_c img{width:120px}

    .main_text,.main_bg{
      text-align:center;
      width:100%
    }
    .main_text h2 img{height:20px;margin-right:10px}
}

@media screen and (max-width: 500px) {
    .main_bg img{
    width:100%
    }
    .main_text h2 img{height:20px;margin-right:10px}
}

.main_text,.main_bg{
  float:left;
  position: relative;
  z-index:1;
}

.ecode{
position: absolute;
bottom:0px;
right:50px
}

.ecode img{height:100px;}

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}


</style>



