<template>
  <div class="home">
    <!-- <h1>{{ msg }}</h1> -->
     <img :src="err_bg">

          <router-link to="/"><el-button type="primary" round>Return</el-button></router-link>

  </div>
</template>

<script>

import err_bg from '@/assets/404.png'


export default {
  name: 'Home',
  data () {
    return {
      err_bg:err_bg
    }
  },
  methods: {
    code_show: function (events) {
        this.ecode = true;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

@media screen and (min-width: 992px) {
    .home{width:400px;margin:auto;padding-top:100px;text-align:center;}
    .home img{width:100%;margin-bottom:70px;}
}

@media screen and (max-width: 992px) {
    .home{width:100%;text-align:center;}
    .home img{width:100%;margin-bottom:30px}


}

@media screen and (max-width: 500px) {

    .main_text h2 img{height:20px;margin-right:10px}
}



</style>



